
import React from 'react';
import { Row, Col, Card, Form, Input, Select, Icon, Button, Dropdown, Menu, InputNumber, DatePicker, Modal, message,Table } from 'antd';
import BreadcrumbCustom from '../BreadcrumbCustom';
import styles from './TableList.less';
import { consumption } from '../../axios';
const FormItem = Form.Item;
const RangePicker = DatePicker.RangePicker;
const { Option } = Select;
const flowColumns = [{
        title: '设备编号',
        dataIndex: 'boxId',
      },{
        title: '手机号',
        dataIndex: 'phone',
      }, {
        title: '消费时间',
        dataIndex: 'time',
      },{
        title: '消费网点',
        dataIndex: 'station',
      },{
        title: '消费金额',
        dataIndex: 'money',
      }];
class Payment extends React.Component {
    constructor(props) {
    super(props);
    this.state={data:[]}
  }
  handleSearch = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err,values) => {
      if (err) {
        return;
      }
      let beginTime=null;
      let endTime=null;
      // Should format date value before submit.
      const rangeTimeValue = values.orderTime;
      if(rangeTimeValue){
         beginTime = rangeTimeValue[0].format('YYYY-MM-DD ');
         endTime = rangeTimeValue[1].format('YYYY-MM-DD ');
      }
      console.log('Received values of form: ', values);
      consumption([values.phone,beginTime,endTime]).then(res => {
        let i = 0;
        if(res&&res.data.status===1){
          this.setState({data: [...res.data.info.map(val => {
                    val.key = ++i;
                    return val;
                })],});
        }     
        });
    });
  }
  handleReset = () => {
    this.props.form.resetFields();
  }
    render() {
        const { getFieldDecorator } = this.props.form; 
        const formItemLayout = {
          labelCol: { span: 6 },
          wrapperCol: { span: 6 },
        };
        return (
            <div className="gutter-example button-demo">
                <BreadcrumbCustom first="查询管理" second="用户消费查询"/>
                <Row gutter={30}>                  
                  <Col className="gutter-row" md={24}>
                        <div className="gutter-box">
                            <Card bordered={false} title="用户消费查询" className={'no-padding'}>
                                <div className="gutter-box">
                                    <Form onSubmit={this.handleSearch} layout="inline">
                                      <Row gutter={{ md: 8, lg: 24, xl: 48 }}>
                                        <Col md={7} sm={24} offset={2}>
                                          <FormItem label="手机号">
                                            {getFieldDecorator('no')(
                                              <Input placeholder="请输入" />
                                            )}
                                          </FormItem>
                                        </Col>
                                        <Col md={7} sm={24}>
                                          <FormItem {...formItemLayout} label={`订单时间`} >
                                          {getFieldDecorator(`orderTime`)(
                                             <RangePicker showTime format="YYYY-MM-DD HH:mm:ss" style={{width:'null'}}/>
                                          )}
                                          </FormItem>
                                        </Col>
                                        <Col md={4} sm={24} style={{marginTop:'20'}}>
                                          <span className={styles.submitButtons}>
                                            <Button type="primary" htmlType="submit">查询</Button>
                                            <Button style={{ marginLeft: 8 }} onClick={this.handleReset}>重置</Button>
                                          </span>
                                        </Col>
                                      </Row>
                                    </Form>
                                  <Row>
                                    <Col md={20} offset={2}>
                                    <p>查询结果：共<span style={{color:'#49a9ee'}}>{this.state.data.length}</span>条消费记录</p>
                                    <Table columns={flowColumns} dataSource={this.state.data}   bordered />
                                    </Col>
                                  </Row>
                                </div>
                            </Card>
                        </div>
                    </Col>                      
                </Row>
            </div>
        )
    }
}

const Pay = Form.create()(Payment);
export default Pay;


