import React from 'react';
import { connect } from 'react-redux';
import moment from 'moment';
import { Row, Col ,Table,Form,Input, Button,Card,Select,DatePicker,Icon  } from 'antd';
import BreadcrumbCustom from '../BreadcrumbCustom';
import styles from '../query/TableList.less';
import { deviceConnectTenSearch } from '../../axios';
const FormItem = Form.Item;
const Option = Select.Option;

const connectColmns = [{
        title: '设备MAC',
        dataIndex: 'imei',
        width:'160px',
        fixed: 'left' 
      },{
        title: '当前状态',
        dataIndex: 'status',
        width:'100px',
        fixed: 'left' 
      }, {
        title: '所属城市',
        dataIndex: 'city',
        width:'100px',
        fixed: 'left' 
      },{
        title: '区域负责人',
        dataIndex: 'area',
        width:'100px',
        fixed: 'left' 
      },{
        title: '负责人手机',
        dataIndex: 'phone',
        width:'160px',
        fixed: 'left' 
      },{
        title: '所属网点',
        dataIndex: 'station',
        width:'160px',
        fixed: 'left' 
      },{
        title: '硬件版本号',
        dataIndex: 'version',
        width:'80px',
        fixed: 'left' 
      },{
        title: '10日连接情况',
        dataIndex: 'connectInfoTen',
      }];
      
class deviceConnectTen extends React.Component {
  constructor(props) {
    super(props);
    this.state={
      data:[],
      cities:this.props.auth.data.stationInfo.area[0][this.props.auth.data.stationInfo.city[0]],
      secondCity:this.props.auth.data.stationInfo.area[0][this.props.auth.data.stationInfo.city[0]][0],
      columns:connectColmns,
    }
  }
  handleProvinceChange = (value) => {
    this.setState({
      cities: this.props.auth.data.stationInfo.area[this.props.auth.data.stationInfo.city.indexOf(value)][value],
      secondCity:this.props.auth.data.stationInfo.area[this.props.auth.data.stationInfo.city.indexOf(value)][value][0]
    });
    this.props.form.setFieldsValue({
      DistrictHead:'全部'
    });
  }
  onSecondCityChange = (value) => {
    this.setState({
      secondCity: value,
    });
  }
  handleSearch = (e) => {
    e.preventDefault();

    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      const momentToday = values['time']?values['time']:moment();
     let  columns  = connectColmns;
     for(let i =1;i<=10;i++){
      let today = momentToday.subtract(1, 'days').format('YYYY-MM-DD');
      const newColumn = {
          title: today,
        children: [{
        title: '信号评价',
        dataIndex: 'Signal'+today,
        key: 'Signal'+today,
        width:'50px',
         }, {
        title: '重连次数',
        dataIndex: 'reConnect'+today,
        key: 'reConnect'+today,
        width:'50px',
        }]
        }
        columns = [...columns,newColumn];
     }
      this.setState({columns:columns}); 
      let beginTime=momentToday.format('YYYY-MM-DD');
      let endTime=momentToday.add(10, 'days').format('YYYY-MM-DD');
      console.log('Received values of form: ', values);
      deviceConnectTenSearch([values.city,values.DistrictHead,values.MAC,values.Dot,beginTime,endTime]).then(res => {
        let i = 0;
        if(res&&res.data.status===1){
          this.setState({data: [...res.data.info.map(val => {
                    val.key = ++i;
                    return val;
                })],});
        }     
        });
      this.props.form.resetFields();
    });
  }
  handleReset = () => {
    this.props.form.resetFields();
  }

  render() {
    const { getFieldDecorator } = this.props.form; 
        const provinceOptions = this.props.auth.data.stationInfo.city.map(province => <Option key={province}>{province}</Option>);
    const cityOptions = this.state.cities.map(city => <Option key={city}>{city}</Option>);
    const formItemLayout = {
      labelCol: { span: 6 },
      wrapperCol: { span: 12 },
    };
    return (
      <div className="gutter-example button-demo">
      <BreadcrumbCustom first="设备管理" second="近10天设备连接性查询"/>
        <div className="gutter-box">
            <Card bordered={false} title="近10天设备连接性查询" className={'no-padding'}>
          <Form onSubmit={this.handleSearch} layout="inline">
            <Row gutter={{ md: 8, lg: 24, xl: 48 }}>
            <Col md={5} sm={24} offset={2}>
                      <FormItem label="所属城市：">
                        {getFieldDecorator('city')(
                         <Select  style={{ width: 90 }} onChange={this.handleProvinceChange}>
                          {provinceOptions}
                        </Select>
                        )}
                      </FormItem>
                    </Col>
                    <Col md={5} sm={24} >
                      <FormItem label="区域负责人：">
                      {getFieldDecorator('DistrictHead')( 
                         <Select  style={{ width: 90 }} onChange={this.onSecondCityChange}>
                          {cityOptions}
                        </Select>
                        )}
                      </FormItem>
                    </Col>
                    <Col md={6} sm={24}>
                    <FormItem  label={`时间`} >
                      {getFieldDecorator(`time`,{
                        initialValue:moment()
                      })(
                        <DatePicker  format="YYYY-MM-DD" />
                           )}
                        </FormItem>
                    </Col> 
                    <Button style={{ marginTop: 20 }} onClick={this.handleReset}><Icon type="download" />导出表格</Button>
                    <Col md={7} sm={24} offset={2}>
                      <FormItem label="设备MAC">
                        {getFieldDecorator('MAC')(
                          <Input placeholder="请输入" />
                        )}
                      </FormItem>
                    </Col>
                    <Col md={7} sm={24}>
                       <FormItem label="网点名：">
                        {getFieldDecorator('Dot')(
                         <Input placeholder="请输入" />
                        )}
                      </FormItem>
                    </Col>             
                    <Col md={4} sm={24} style={{marginTop:20}} offset={2}>
                      <span className={styles.submitButtons}>
                        <Button type="primary" htmlType="submit">查询</Button>
                        <Button style={{ marginLeft: 8 }} onClick={this.handleReset}>重置</Button>
                      </span>
                    </Col>
                  </Row>
            </Form>
         <Row>
          <Col md={20} offset={2}>
          <p>查询结果：共<span style={{color:'#49a9ee'}}>{this.state.data.length}</span>台设备</p>
          <Table columns={this.state.columns} dataSource={this.state.data} scroll={{ x: 1860}}  bordered />
          </Col>
        </Row>
        </Card>
        </div>
      </div>  
    );
  }
}
const mapStateToProps = state => {
    const { auth = {data: {}} ,dotEquipment = {data: {}}} = state.httpData;
    return { auth,dotEquipment };
};
const DeviceConnectTen = Form.create()(deviceConnectTen);
export default connect(mapStateToProps)(DeviceConnectTen);



